<template>
	<view class="bg-color">
		<camera device-position="front" @error="error" style="width: 0px; height: 0px" v-if="showCamera"></camera>
		<view class="uLine-progress">
			<view v-if="classCount > 0" style="text-align: center; color: #2fba2f; margin-bottom: 10px;">量表: {{firstfloorindex}}/{{classCount}}</view>
			<view style="width:85%;display: inline-block;float: left;">
				<u-line-progress :percent="secondfloorper" inactiveColor="#92B473" activeColor="#628D3D" :show-percent="false" height="22"></u-line-progress>
			</view>
			<view style="float: right;color: #628D3D;font-size: 28rpx;float: left;margin-top: 10rpx;margin-left: 10rpx;">{{questionIndex+1}}/{{questionList.length}}</view>
		</view>
		<view style="background-color: #ffffff; border-radius: 20rpx; width: 80%; height: 900rpx;margin: 0 auto;">
			<view style="width: 95%;padding: 30rpx 0;margin: 0 auto;">
				<view class="titleNew">
					<text v-if="questionList.length > 0">{{questionIndex+1}}、{{questionList[questionIndex].content}}</text>
					<text v-else>题目加载中</text>
				</view>
				<!--单选题-->
				<view v-if="questionList[questionIndex].questionType=='1'">
					<view :class="{'active':item.answerchecked}" class="topic-btn"
						@click="answerclick(item)"
						v-for="(item,index) in items" 
						:key="item.selectContent">
						{{ item.selCode }}. {{ item.selectContent }}
					</view>
				</view>
				<!--问答题-->
				<view v-if="questionList[questionIndex].questionType=='2'">
					<textarea placeholder-style="color:#969592" style="border: 1rpx solid #92d6ff; width: 100%; border-radius: 10px;" 
					placeholder="请输入答案" :value="questionList[questionIndex].rightAnswer" @input="inputAnswer($event, questionIndex)"/>
				</view>
				<!--多选题-->
				<checkbox-group @change="mulSelChange"  v-if="questionList[questionIndex].questionType=='3'">
					<label class="titleNew" v-for="item in items" :key="item.selCode">
						<view>
							<checkbox :value="item.selCode" :checked="item.answerchecked" color="#55aaff"/>
						</view>
						<view style="font-size: 15px; color: #74a9ff;">{{item.selCode}}.{{item.selectContent}}</view>
					</label>
				</checkbox-group>
			</view>
		</view>
		<view>
			<view class="tabbar-bottom">
				<span class="buy-button" v-on:click="back" v-show="questionIndex">
					<span>{{'上一题'}}</span>
				</span>
				<span class="buy-button" v-on:click="nextQuestion" 
				v-show="(questionList[questionIndex].questionType=='2' || questionList[questionIndex].questionType=='3') && questionIndex < questionList.length - 1">
					<span>{{'下一题'}}</span>
				</span>
				<span class="buy-button" v-show="questionIndex == questionList.length - 1">
					<span @click="submit" :disabled="lock">{{'提交'}}</span>
				</span>
			</view>
		</view>
	</view>
</template>

<script>
	import userCheck from '@/utils/userAction.js';
	export default {
		data() {
			return {
				firstfloorindex: 0,
				questionIndex: 0,
				capitalindex: ["一", "二", "三", "四", "五", "六"],
				questionList: [],
				lock: false,
				title: '',
				source: '',
				curTimeStamp: 0,
				classCount: 0,
				classId: '',
				videoAnalysis: false,
				monitorTimer: {},
				healthAnalysisResult: {},
				showCamera: false,
				cameraContext: {},
				reviewRecordKey: '',
				evalCode:'',
				select:'',
				school:'',
				userId:'',
				name:'',
				sex:'',
				age:'',
				paramListAgain:[]
			}
		},
		onUnload() {
			if(this.source != "1") {
				uni.switchTab({
					url:  '/pages/index/index',
					success(res) {
						console.log(res)
					},
					fail(err) {
						console.log(err)
					}
				});
			}
			//清理定时器
			if (this.videoAnalysis) {
				this.closeCamera()
				clearTimeout(this.monitorTimer)
			}
		},
		onLoad(e) {
			this.classId = e.classId
			this.reviewRecordKey = this.classId + "_record"
			this.firstfloorindex = this.getNextIndex(e.classId, true)
			this.questionIndex = 0
			this.cursor = 0
			const that = this
			if(e.source) {
				this.source = e.source
			}
			if (e.title){
				this.title = decodeURIComponent(e.title)
			}
			if(e.userId){
				this.userId = e.userId
			}
			if(e.name){
				this.name = decodeURIComponent(decodeURIComponent(e.name))
			}
			if(e.sex){
				this.sex = e.sex
			}
			if(e.age){
				this.age = e.age
			}
			if(e.evalCode){
				this.evalCode = decodeURIComponent(decodeURIComponent(e.evalCode))
			}
			if(e.select){
				this.select = decodeURIComponent(decodeURIComponent(e.select))
			}
			if(e.school){
				this.school = decodeURIComponent(decodeURIComponent(e.school))
			}
			this.loadData(e.classId)
			this.videoAnalysis = e.videoAnalysis && e.videoAnalysis == 1
			if (this.videoAnalysis) { //要进性视频监控分析
				let that = this
				uni.showModal({
					title: '提示',
					content: '测评过程中将通过视频监测您的心率变化，请认真做答',
					icon: 'none',
					mask: true,
					showCancel: false,
					success: function (res) {
						if (res.confirm) {
							that.showCamera = true
							that.cameraContext = uni.createCameraContext()
							setTimeout(function() {
								that.startRecord()
							}, 1000)
							
						}
					}
				})
			}
		},
		methods: {
			inputAnswer($e, index) {
				this.questionList[index]["rightAnswer"] = $e.target.value
			},
			startRecord() {
				let that = this
				this.cameraContext.startRecord({
					success: function(res) {
						console.log("question startRecord success:" + JSON.stringify(res))
						that.videoMonitor()
					},
					fail: function(err) {
						console.log("question startRecord fail，", err)
						uni.showToast({
							title: '摄像头开启失败',
							icon: 'none'
						})
						
					}
				})
			},
			//视频监控
			videoMonitor() {
				console.log("videoMonitor begin....")
				let that = this
				this.monitorTimer = setTimeout(function(){
					that.executeAnalysis()
				}, 16000)
			},
			//视频分析
			executeAnalysis() {
				console.log("executeAnalysis begin ----")
				let userData = uni.getStorageSync('userData')
				let projectId = uni.getStorageSync("projectId")
				let that = this
				this.cameraContext.stopRecord({
					success: function(res) {
						console.log("question stopRecord success: ", res)
						//上传视频文件
						uni.uploadFile({
							url: that.$apis.analysisVideoUrl,
							filePath: res.tempVideoPath,
							fileType: 'video',
							name: 'file',
							formData: {'classId': that.classId, 'projectId': projectId},
							header:{"Content-Type": "multipart/form-data", "userId": userData.userId},
							success: (res) => {
								console.log('question videoAnalysis success, res is:', res)
								if(res.statusCode == 200) {
									let data = JSON.parse(res.data)
									if(data.code == 200) {
										let resultObj = data.result
										if(resultObj.healthStatus == 1) {
											let oldResult = uni.getStorageSync("healthAnalysisResult")
											let newResult = resultObj.healthAnalysisResult.result
											//计算变化率
											let oldHr = oldResult.hr
											let newHr = newResult.hr
											let diffRate = Math.abs(newHr - oldHr) / oldHr
											console.log("oldHr: " + oldHr + ", newHr: " + newHr + ", diffRate=" + diffRate)
											if (diffRate >= 0.15) {
												uni.showModal({
													title: '提示',
													content: '您测评过程中的心率变化超过15%，请认真做答',
													showCancel: false,
													success: function (res) {
														if (res.confirm) {
															that.startRecord()
														}
													}
												})
												return
											}
											that.startRecord()
											return
										}
									}
								}
								uni.showModal({
									title: '提示',
									content: '请确保测评过程中摄像头对准脸部',
									showCancel: false,
									success: function (res) {
										if (res.confirm) {
											that.startRecord()
										}
									}
								})
							},
							fail: (err) => {
								console.log('videoAnalysis fail', err)
								that.startRecord()
							}
						})
					},
					fail: function(err) {
						that.startRecord()
						console.log("stopRecord fail:", err)
					}
				})
			},
			error(e) {
				console.log(e.detail)
			},
			//加载数据
			loadData(classId, classTitle) {
				uni.setNavigationBarTitle({
					title: classTitle
				})
				uni.showLoading({
					title: "数据加载中"
				})
				let that = this
				this.$apis.postQuestions({'classId': classId}).then(res => {
					uni.hideLoading()
					that.questionList = res.result
					that.setReviewProgress()
				})
			},
			setReviewProgress() {
				let classReviewRecord = uni.getStorageSync(this.classId + "_record")
				if (classReviewRecord && classReviewRecord != {}) {
					let totalRecord = 0
					for (let i=0; i < this.questionList.length; i++) {
						let question = this.questionList[i]
						if(classReviewRecord[question.questionId]) {
							if(question.questionType == "2") { //问答题
								question["rightAnswer"] = classReviewRecord[question.questionId].rightAnswer
							} else {
								question["selCode"] = classReviewRecord[question.questionId].selCode
								question["selectGrade"] = classReviewRecord[question.questionId].selectGrade
							}
							totalRecord++
						}
					}
					this.questionIndex = totalRecord
				}
			},
			//下一题
			nextQuestion() {
				let curQuestion = this.questionList[this.questionIndex]
				if(curQuestion.questionType=='2' && (!curQuestion.rightAnswer || curQuestion.rightAnswer == '')) {
					uni.showToast({
						title: "请输入答案",
						icon: "none"
					})
					return
				}
				//多选题
				if(curQuestion.questionType=='3') {
					let checked = false
					for (let i=0; i < this.items.length; i++) {
						if(this.items[i].answerchecked) {
							checked = true
							break
						}
					}
					if(!checked) {
						uni.showToast({
							title: "请选择答案",
							icon: "none"
						})
						return
					}
				}
				//保存测评记录
				this.saveReviewProgress()
				this.questionIndex++;
			},
			//保存测评进度：针对项目 测评到哪一量表 哪一题
			saveReviewProgress() {
				let projectId = uni.getStorageSync("projectId")
				if(!projectId) {
					projectId = 0
				}
				//如果是项目测评 就保存当前测评进度
				if (projectId > 0 || this.classId == '402880f082eecb960182eee3b1ef0001') {
					//存储当前量表
					uni.setStorageSync("currentClassId", this.classId)
					let classReviewRecord = uni.getStorageSync(this.reviewRecordKey)
					if(!classReviewRecord) {
						classReviewRecord = {}
					}
					let classQuestionAnswer = {}
					let curQuestion = this.questionList[this.questionIndex]
					classQuestionAnswer["questionId"] = curQuestion.questionId
					classQuestionAnswer["selCode"] = curQuestion.selCode
					classQuestionAnswer["selectGrade"] = curQuestion.selectGrade
					if (curQuestion.questionType == 2) {
						classQuestionAnswer["rightAnswer"] = curQuestion.rightAnswer
					}
					classReviewRecord[curQuestion.questionId] = classQuestionAnswer
					uni.setStorageSync(this.reviewRecordKey, classReviewRecord)
				}
			},
			closeCamera() {
				this.cameraContext.stopRecord({})
				this.showCamera = false
				uni.removeStorageSync("healthAnalysisResult")
			},
			//提交问题
			submit() {
				//清理定时器
				if (this.videoAnalysis) {
					this.closeCamera()
					clearTimeout(this.monitorTimer)
				}
				let curQuestion = this.questionList[this.questionIndex]
				if(curQuestion.questionType == '1' || curQuestion.questionType == '3') {
					var havechecked = false
					let curItem = {}
					for (let i=0; i<curQuestion.selectList.length; i++) {
						curItem = curQuestion.selectList[i]
						if(curItem.answerchecked) {
							havechecked = curItem.answerchecked
							break;
						}
					}
					if(!havechecked) {
						uni.showToast({
							title: "请选择答案",
							icon: "none"
						})
						return;
					} else {
						this.answerclick(curItem)
					}
				} else if(curQuestion.questionType == '2') {
					if(!curQuestion.rightAnswer || curQuestion.rightAnswer == '') {
						uni.showToast({
							title: "请填写答案",
							icon: "none"
						})
						return;
					} else {
						//提交数据
						this.answerclick({"selCode": ""})
					}
				}
			},
			//多选框
			mulSelChange: function(option) {
				let curQuestion = this.questionList[this.questionIndex]
				var selCodes = option.detail.value
				let selGrades = 0
				for (var i = 0, lenI = this.items.length; i < lenI; ++i) {
					const item = this.items[i]
					if(selCodes.includes(item.selCode)){
						this.$set(item,'answerchecked',true)
						selGrades += parseFloat(item.selectGrade)
					} else {
						this.$set(item,'answerchecked',false)
					}
				}
 				curQuestion["selCode"] = selCodes.join(",")
				curQuestion["selectGrade"] = selGrades
			},
			//点击选项
			answerclick: function(option) {
				if (this.lock) {
					console.log("lock is true now return")
					return
				}
				if (!option) {
					return
				}
				this.lock = true
				let curQuestion = this.questionList[this.questionIndex]
				if(curQuestion.questionType == '1') {
					try {
						//console.log("num=" + this.questionIndex + "&selCode=" + option.selCode + "&selectGrade=" + option.selectGrade)
						if(option.selCode) {
							curQuestion["selCode"] = option.selCode;
						}
						if(option.selectGrade) {
							curQuestion["selectGrade"] = option.selectGrade;
						}
						var eachanswers = curQuestion.selectList
						var havechecked = eachanswers.some(function(item) {
							return item.answerchecked == true
						});
						if (havechecked) {
							eachanswers.map(function(item) {
								item.answerchecked = false
								return item
							})
						}
						option.answerchecked = true
					} catch (e) {
					   console.log(e)
					}
				}
				let that = this
				setTimeout(() => {					//保存测评记录
					this.saveReviewProgress()
					if (this.questionIndex < this.questionList.length - 1) {
						this.questionIndex++
						that.lock = false
					} else {
						uni.showLoading({
							title: '测评完成，提交中',
						})
						var resultList = []
						let projectId = uni.getStorageSync("projectId")
						if(!projectId) {
							projectId = 0
						}
						let dongliangClassId = '402880f082eecb960182eee3b1ef0001';
						let dongliangClassProId = '2c9cff928408eab3018413a00d8a006a';
						if(this.classId == dongliangClassId || this.classId == dongliangClassProId){//栋梁测评
							for (var i = 0; i < this.questionList.length; i++) {
								var q = this.questionList[i]
								var obj = {
									'quesNo': q.questionNum,
									'answer': q.selCode,
									'scoreA': q.selectGrade,
									'scoreB':"0"
								}
								resultList.push(obj)
							}
							let userData = uni.getStorageSync('userData')
							let name = userData.name
							var objUser = {
								"serialNum": '130304202206261503435',
								"name": this.name,
								"sex": this.sex,
								"age": this.age,
								"userId":this.userId,
								"select":this.select,
								"school":this.school
							}
						
							var paramList = []
							var version  = ''
							if(this.classId == dongliangClassId){
								version = 1
							}
							if(this.classId == dongliangClassProId){
								version = 2
							}
							var objnew = {
								'testCode':this.evalCode,
								'userInfo':objUser,
								'testRecord':resultList,
								'classId':this.classId,
								'projectId':projectId,
								'version':version
							}
							paramList.push(objnew)
							//let pdfUrl = 'https://www.zhuxinkang.com/review/upload2/PDF/create/2022/09/20/cp-20220920204836310.pdf'						
							//提交测评数据
							this.$apis.postCommitTest(paramList).then(res => {
								that.lock = false
								uni.hideLoading()
								if (res.code == 200) {
									uni.removeStorageSync("currentClassId")
									uni.removeStorageSync(this.reviewRecordKey)
									//跳转报告查看页面
									uni.navigateTo({
										url: '/pages/report/pdfreport?pdfUrl=' + encodeURIComponent(res.pdfUrl)
									})
								} else {
									uni.showToast({
										title: '提交失败',
										icon: 'wrong'
									})
									uni.removeStorageSync("currentClassId")
									uni.removeStorageSync(this.reviewRecordKey)
								}
							}).catch(err => {
								console.log('进入异常')
								uni.hideLoading()
								that.lock = false
								//重新输入测评码
								uni.setStorageSync("paramList", paramList)
								uni.navigateTo({
									url: '/pages/review/testCodeAagin'
								})
								
							})
							
						}else{
							for (var i = 0; i < this.questionList.length; i++) {
								var q = this.questionList[i]
								var obj = {
									'selectGrade': q.selectGrade, 
									'content': q.content, 
									'questionId': q.questionId, 
									'questionNum': q.questionNum, 
									'selCode': q.selCode, 
									'variateId': q.variateId, 
									'variateName': q.variateName, 
									'classId': q.classId,
									'projectId': projectId
								}
								if(q.rightAnswer) {
									obj["rightAnswer"] = q.rightAnswer
								}
								resultList.push(obj)
							}
							//提交测评数据 
							this.$apis.postCompleteReview(resultList).then(res => {
								let projectId = uni.getStorageSync("projectId")
								that.lock = false
								uni.hideLoading()
								if (res.code == 200) {
									uni.showToast({
										title: '测评完成，报告已生成',
										icon: 'right'
									});
									that.finished(res.result.classId, res.result.resultId, that.title,projectId)
									// uni.navigateTo({
									// 	url: '/pages/report/report?resultId=' + res.result.resultId + "&title=" + that.title
									// });
								} else {
									uni.showToast({
										title: '测评报告生成失败',
										icon: 'wrong'
									});
								}
							}).catch(err => {
								uni.hideLoading()
								that.lock = false
								console.log(err)
							})
						}
					}
				}, 300)
			},
			getNextIndex: function(classId, last) {
				let projectClass = uni.getStorageSync("projectClass")
				let a = -1
				if (projectClass) {
					this.classCount = projectClass.length
					//获取下一个测评量表
					for (let i=0; i < projectClass.length; i++) {
						let reviewClass = projectClass[i]
						if(reviewClass.classId == classId && i < projectClass.length -1){
							a = i + 1
							break
						}
						if (last && reviewClass.classId == classId && i == projectClass.length -1) {
							return i + 1
						}
					}
				}
				return a
			},
			finished: function(classId, resultId, title,projectId) { //完成测评处理
				let projectClass = uni.getStorageSync("projectClass")
				let a = this.getNextIndex(classId, false)
				//跳到下一个量表测试
				if (a > -1) {
					//提示
					uni.showToast({
						title: "测评完成，即将进入下一个量表测试",
						duration: 2000
					})
					let nextInfo = projectClass[a]
					this.questionList = []
					this.questionIndex = 0
					//设置当前下一个量表测评ID
					uni.setStorageSync("currentClassId", nextInfo.classId)
					//移除上一个量表缓存
					uni.removeStorageSync(this.reviewRecordKey)
					//跳到完成页面
					uni.redirectTo({
						url: '/pages/report/guide?classId=' + nextInfo.classId
					})
					//this.loadData(reviewClass.classId, reviewClass.title)
				} else {
					//移除上一个量表缓存
					uni.removeStorageSync("currentClassId")
					uni.removeStorageSync(this.reviewRecordKey)
					//跳到完成页面
					uni.redirectTo({
						url: '/pages/report/finished?classId=' + classId + "&resultId=" + resultId + "&title=" + title + "&projectId=" + projectId
					})
				}
			},
			back: function() {
				this.questionIndex--;
			}
		},
		computed: {
			secondfloorper: function() {
				return parseInt((((this.questionIndex + 1) / (this.questionList.length))
					.toFixed(2) * 100));
			},
			items: function() {
				if (this.questionList.length > 0) {
					return this.questionList[this.questionIndex].selectList;
				}
				return []
			}
		}
	}
</script>

<style scoped>
	page {
		background-color: #EFE4C8;
	}
	.active{
		background-color: #92B473;
	}

	.conttop {
		display: flex;
		justify-content: space-between;
	}

	.conttopl text {
		vertical-align: middle;
		padding-left: 8rpx;
	}

	.progress-box {
		margin-top: 20rpx;
		margin-bottom: 20rpx;
